<template>
  <div class="sjzxMain" id="hello">
    <div class="sjzx-main-top">
      <div class="sjzx-main-top-jjbh">
        <div class=" input-group" style="width: 300px;margin-top: 8px;">
          <span class="input-group-addon">一键查询</span>
          <input class="form-control" type="text"  v-model="moHu" placeholder="请输条件...">
        </div>
      </div>
      <div class="sjzx-main-top-btn" v-on:click="select">查  询</div>
    </div>
    <div class="sjzx-main-center">
      <table>
        <thead>
        <tr>
          <th style="width: 165px;line-height: 60px;border: lightskyblue 1px solid">姓名</th>
          <th style="width: 205px;line-height: 60px;border: lightskyblue 1px solid">密码</th>

        </tr>
        </thead>
        <tbody>
        <tr v-for="item in mjAll" v-bind:key="item.username">
          <td style="width: 155px;line-height: 60px;border: lightskyblue 1px solid">{{item.username}}</td>
          <td style="width: 205px;line-height: 60px;border: lightskyblue 1px solid">{{item.password}}</td>
        </tr>
        </tbody>
      </table>
    </div>
    <div class="sjzx-main-bottom">
      <div id="sjzx-page" class="sjzx-page"></div>
    </div>
  </div>
</template>

<script>

import axios from 'axios'
axios.defaults.baseURL = 'http://localhost:8080'
// axios.defaults.baseURL = 'http://localhost:8080/TEST0529'
export default {
  name: 'HelloWorld',
  data () {
    return {
      msg: 'Welcome to Your Vue.js App',
      moHu: '',
      mjAll: [],
      pageSize: '5'
    }
  },
  methods: {
    select () {
      let _this = this
      _this.$options.methods.querymj(_this, 1, _this.pageSize, _this.moHu)
    },
    querymj (obj, pageNum, pageSize, moHu) {
      axios.post('/user/getAllUser',
        {
          pageNum: pageNum,
          pageSize: pageSize,
          moHu: moHu,
          pageInfo: {'pageNum': pageNum, 'pageSize': pageSize}})
        .then(function (infoResp) {
          console.log('请求到的数据是')
          console.log(infoResp)
          let count = infoResp.data.data.total
          obj.mjAll = infoResp.data.data.list

          if (count > 0) {
            let _this = obj
            window.layui.use(['laypage'], function () {
              let laypage = window.layui.laypage
              laypage.render({
                elem: 'sjzx-page',
                count: count,
                limit: pageSize,
                curr: pageNum,
                layout: ['count', 'prev', 'page', 'next', 'refresh', 'skip'],
                theme: '#5EB660',
                jump: function (obj, first) {
                  if (!first) {
                    console.log('这是第' + obj.curr + '页')
                    _this.$options.methods.querymj(_this, obj.curr, _this.pageSize, _this.moHu)
                  }
                }
              })
            })
            console.log('有数据')
          } else {
            console.log('没有数据')
          }
          count = 0
        })
        .catch(function (error) {
          console.log(error)
        })
    }
  },
  created: function () {
  },
  mounted: function () {
    // axios.get('/user/getAll')
    //   .then(function (response) {
    //     console.log(response)
    //   })
    //   .catch(function (error) {
    //     console.log(error)
    //   })
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  .sjzx-main-top {
    border: 1px solid skyblue;
    width: 90%;
    margin-left: 5%;
    display: flex;
    background-color: whitesmoke;
    border-radius: 10px;
  }
  .sjzx-main-center {
    width: 90%;
    margin-left: 5%;
  }
  .sjzx-main-top-jjbh {
    flex: 5;
    line-height: 100px;
  }
  .sjzx-main-top-btn {
    flex: 2;
    line-height: 116px;
  }
</style>



